.o-editor {
    .block-editor-block-list__block[data-type="custom/wpe-column"] {
        width: 100%;
        margin-top: 0 !important;
        margin-bottom: 0 !important;
        box-sizing: border-box;
        padding-top: 60px !important;

        &.is-empty {
            padding-top: 80px !important;
            border: 1px dashed $o-accent-color;
        }

        & > .block-list-appender {
            width: 100%;
            max-width: 100%;
        }

        &:hover {
            & > .block-list-appender {
                visibility: visible;
            }

            & > .block-editor-block-list__block {
                z-index: 20;
            }
        }

        &:not([contenteditable])::focus::after {
            border: none;
        }
    }
}

.o-editor-app {
    .basicField {
        .column-layout {
            .flex {
                & > .basicField {
                    margin-right: 5%;

                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }
    }
}
